"use client";

import { SidebarItemTypes } from "@/types";
import {
  FileOutlined,
  FolderOpenOutlined,
  HomeOutlined,
  OrderedListOutlined,
  ProductFilled,
  ToolOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { Menu } from "antd";
import Link from "next/link";
import { usePathname } from "next/navigation";

export default function Sidebar() {
  const pathname = usePathname();

  return (
    <aside className="h-full w-64 p-2 border-r-2 border-gray-200 rounded-xl bg-gray-800">
      <Menu
        className="h-full"
        style={{
          padding: 0,
          height: "100%",
          borderRadius: "8px",
        }}
        selectedKeys={[pathname]}
        mode="inline"
        theme="dark"
      >
        {[
          {
            key: "/",
            icon: <HomeOutlined />,
            label: <Link href={"/"}>Home</Link>,
          },
          {
            key: "/students",
            icon: <UserOutlined />,
            label: <Link href={"/students"}>Users</Link>,
          },
          {
            key: "/groups",
            icon: <FolderOpenOutlined />,
            label: <Link href={"/groups"}>Groups</Link>,
          },
          {
            key: "/products",
            icon: <FileOutlined />,
            label: <Link href={"/products"}>Products</Link>,
          },
          {
            key: "/categories",
            icon: <ProductFilled />,
            label: <Link href={"/categories"}>Categories</Link>,
          },
          {
            key: "/orders",
            icon: <OrderedListOutlined />,
            label: <Link href={"/orders"}>Buyurtmalar</Link>,
          },
        ].map((item) => (
          <Menu.Item key={item.key} icon={item.icon}>
            {item.label}
          </Menu.Item>
        ))}
      </Menu>
    </aside>
  );
}
