"use client";

import useAuthStore from "@/store/MyAuthState";
import {
  ExclamationCircleFilled,
  ExclamationOutlined,
  LogoutOutlined,
  UserOutlined,
} from "@ant-design/icons";
import { Dropdown, Menu, Button, MenuProps, message, Tooltip } from "antd";
import { Span } from "next/dist/trace";
import Image from "next/image";
import React from "react";

export default function Header() {
  const MyAuthState = useAuthStore();

  const items: MenuProps["items"] = [
    {
      label: MyAuthState.user?.role,
      key: "1",
      icon: <UserOutlined />,
    },
    {
      label: MyAuthState.user?.email,
      key: "2",
      icon: <ExclamationCircleFilled />,
    },

    {
      label: (
        <span
          onClick={() => {
            localStorage.removeItem("auth");
            useAuthStore.setState({ token: "", user: null });
          }}
        >
          Logout
        </span>
      ),
      key: "4",
      icon: <LogoutOutlined />,
      danger: true,
    },
  ];

  const menuProps = {
    items,
  };

  return (
    <header className="p-6 flex items-center justify-between bg-green-500 text-white border-2 border-blue-700 rounded-xl shadow-lg">
      <h1 className="text-4xl font-bold italic">Logo</h1>

      <div className="flex items-center gap-4">
        <Image
          src="https://www.pngmart.com/files/21/Account-User-PNG-HD.png"
          width={64}
          height={64}
          className="w-16 h-16 rounded-full border-2 border-white transition-transform transform hover:scale-110"
          alt="Account image"
        />
        <Dropdown.Button
          menu={menuProps}
          buttonsRender={([leftButton, rightButton]) => [
            <Tooltip title="tooltip" key="leftButton">
              {leftButton}
            </Tooltip>,
            React.cloneElement(rightButton as React.ReactElement<any, string>, {
              loading: false,
            }),
          ]}
        >
          {MyAuthState.user?.name}
        </Dropdown.Button>
      </div>
    </header>
  );
}
